.student-profile-wrapper {
  width: 90%;
  height: 90%;
  overflow: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  // display: flex;
  // flex-direction: row;
  // flex-wrap: wrap;
  // flex: 1;
  // overflow: auto;
  display: grid !important;
  grid-template-columns: repeat(5, 1fr) !important;
  .student-profile-item {
    padding: 30px;
    margin: 30px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-content: center;
    border-radius: 50px;
    img {
      margin-bottom: 1rem;
      align-self: center;
    }
    span {
      font-size: 20px;
      font-weight: 700;
    }
  }
}
